import "./App.css";
import SignInOutContainer from "./container";
import PricingComponent from "./pricingComp";
import PaymentPage from "./paymentPage";
import ActivePage from "./activePage";
import { BrowserRouter, Routes, Route } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route exact path="/Stripe_Assignment_RichPanel/" element={<SignInOutContainer />} />
          <Route exact path="/Stripe_Assignment_RichPanel/payment" element={<PaymentPage />} />
          <Route exact path="/Stripe_Assignment_RichPanel/pricing" element={<PricingComponent />} />
          <Route exact path="/Stripe_Assignment_RichPanel/status" element={<ActivePage />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
